apis.com/css2?family=Boogaloo&display=swap');\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n/* skip to content */\n\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 10px;\n  background: #fff;\n  color: #000;\n  padding: 10px 15px;\n  z-index: 200;\n  transition: all 0.3s ease-in-out;\n  text-decoration: none;\n}\n\n.skip-link:focus {\n  top: 10px;\n}\n\n/* Header */\n\nheader {\n  position: fixed;\n  z-index: 100;\n  left: 0;\n  top: 0;\n  width: 100%;\n}\n\nnav {\n  background-color: #fff;\n  color: #e7e7e7;\n  display: flex;\n  top: 0;\n  align-items: center;\n  position: sticky;\n  border-bottom-width: 1px;\n  border-bottom-style: solid;\n  width: 100%;\n  font-family: 'Boogaloo', cursive;\n  padding: 15px 100px 15px 100px;\n  justify-content: space-between;\n}\n\nnav li {\n  display: inline;\n  padding: 0 10px 0 10px;\n  list-style-type: none;\n  font-family: 'Poppins',\n    sans-serif;\n}\n\nnav li a {\n  padding: 9px;\n  display: inline-block;\n  font-size: 18px;\n  font-weight: 600;\n  text-decoration: none;\n  color: #1B2631;\n}\n\n.navigation {\n  display: flex;\n  width: 100%;\n  padding: 0px;\n  padding-bottom: 0;\n}\n\n.name {\n  color: #1B2631;\n  font-size: 39px;\n  font-weight: bolder;\n  text-decoration: none;\n}\n\n.nav a:hover {\n  color: #95A5A6;\n}\n\n.logo {\n  font-size: 30px;\n  color: black;\n  white-space: nowrap;\n  cursor: pointer;\n}\n\n#hamburger {\n  font-size: 40px;\n  padding: 7px;\n  text-decoration: none;\n  color: black;\n  display: none;\n}\n\n/* Hero */\n\n.hero {\n  display: flex;\n  align-items: center;\n  min-height: 500px;\n  width: 100%;\n  text-align: center;\n  background-image: linear-gradient(rgba(4, 9, 30, 0.7), rgba(4, 9, 30, 0.7)), url(\"../public/images/heros/hero-image_1-max.jpg\");\n  background-position: center;\n  background-color: green;\n}\n\n.hero__inner {\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.hero__title {\n  color: #fff;\n  font-weight: 600;\n  font-size: 36px;\n}\n\n.hero__tagline {\n  color: #fff;\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 300;\n}\n\n.hero button {\n  padding: 13px 25px 13px 25px;\n  outline: none;\n  background-color: transparent;\n  border: 1px solid #fff;\n  color: #fff;\n  border-radius: 5px;\n  cursor: pointer;\n  font-size: 18px;\n  font-weight: 600;\n  margin-top: 30px;\n}\n\n.hero button:hover {\n  background-color: #fff;\n  color: #000;\n}\n\n.hero button:focus {\n  background-color: #fff;\n  color: #000;\n}\n\n/* loader */\n\n.loader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: #f7f9fb;\n  transition: opacity 0.75s, visibility 0.75s;\n  z-index: 999;\n}\n\n.loader-hidden {\n  opacity: 0;\n  visibility: hidden;\n}\n\n.loader::after {\n  content: \"\";\n  width: 75px;\n  height: 75px;\n  border: 15px solid #dddddd;\n  border-top-color: #2E4053;\n  border-radius: 50%;\n  animation: loading 0.75s ease infinite;\n}\n\n@keyframes loading {\n  from {\n    transform: rotate(0deg);\n  }\n\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* Body */\n\nbody {\n  font-family: 'Poppins', sans-serif;\n  margin: 0;\n  padding: 0;\n}\n\nmain:focus {\n  outline: none;\n}\n\n/* Content */\n\n.content {\n  padding: 0 100px 0 100px;\n  width: 100%;\n  margin: 60px auto;\n  text-align: center;\n}\n\n.content__label {\n  font-size: 32px;\n  font-weight: bold;\n}\n\n.content__label::after {\n  content: '';\n  margin-top: 16px;\n  display: block;\n  border-bottom: 1px solid #eeeeee;\n}\n\n/*\n* post\n*/\n\n.posts {\n  margin: 32px auto auto;\n  text-align: left;\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 18px 16px;\n}\n\n#posts{\n  margin-top: 40px !important;\n}\n\n/*\n* post item\n*/\n\n.post-item {\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: 100%;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.post-item img {\n  object-fit: cover;\n  hei